/* eslint-disable */
<template>
    <form class="login100-form validate-form">
      <span class="login100-form-title p-b-59">Log In</span>

      <div class="wrap-input100 validate-input" data-validate="Email is required">
        <span class="label-input100">Email</span>
        <input
          class="input100"
          type="text"
          name="email"
          placeholder="Email..."
          @change="set_infos()"
          v-model="$props.user.email"
        />
        <span class="focus-input100"></span>
      </div>

      <div class="wrap-input100 validate-input" data-validate="Password is required">
        <span class="label-input100">Password</span>
        <input
          class="input100"
          type="password"
          name="pass"
          placeholder="*************"
          @change="set_infos()"
          v-model="$props.user.password"
        />
        <span class="focus-input100"></span>
      </div>

      <div class="flex-m w-full p-b-33">
        <div class="contact100-form-checkbox">
          <input class="input-checkbox100" id="ckb2" type="checkbox" name="remember-me" />
          <label class="label-checkbox100" for="ckb2">
            <span class="txt1">Remember me</span>
          </label>
        </div>
      </div>
    </form>

</template>
<script>
import { initialize } from '../assets/js/main.js'
export default {
  name: 'LoginComponent',
  template: '<login-component/>',
  props: ['user'],
  data () {
    return {

    }
  },

  mounted: function () {
    initialize()
  },
  methods: {
    set_infos () {
      this.$emit('logInfosChanged', this.$props.user)
    }
  },
  errorCaptured (err, vm, info) {
    console.log(`ErrorCaptured: ${err.toString()}\ninfo: ${info}`)
    return false
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



