<template>
  <div>
    <nav-bar v-if="account.status.loggedIn || true" />
    <profile-header />

    <b-container-fluid style="position:relative">
    <b-row >
      <b-col md="3" class="mt-5"  >
        <b-row class="mx-2">
          <b-card>
            <b-card-header>
              <span class="card-title">{{profile.name}} {{profile.familyName}}</span>
            </b-card-header>
            <b-card-body>
              <p>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Consequuntur dolorum magni numquam, beatae, tenetur impedit voluptatem iste repellat similique accusantium ut sunt? Sit neque delectus distinctio reprehenderit pariatur? Laudantium voluptas vitae officiis animi dolore, natus quas! Obcaecati deserunt exercitationem magni delectus sapiente. Ipsa amet vitae repellat eaque rem. Quod facere optio, voluptas temporibus aspernatur qui fugiat quia aut veritatis impedit corrupti dolor enim aperiam quasi vel provident voluptatum accusamus soluta velit quo, ratione cum? Asperiores voluptate consectetur possimus illum maiores?
              </p>

            </b-card-body>
          </b-card>
        </b-row>

      </b-col>
      <b-col  md="6" align-self="center" >
        <b-row class="mt-5" id="sections">
          <b-col align-self="center">
            <div>
              <b-tabs content-class="mt-3" justified align="center">
                <b-tab title="Posts" active>
                  <b-container>
                    <post-template/>
                  </b-container>
                </b-tab>
                <b-tab title="Gallery">
                  <b-row>I'm the second tab</b-row>
                </b-tab>
                <b-tab title="Very, very long title">
                  <b-row>I'm the tab with the very, very long title</b-row>
                </b-tab>
              </b-tabs>
            </div>
          </b-col>
        </b-row>
      </b-col>

    </b-row>
    </b-container-fluid>
    <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. At voluptates eligendi facilis minima minus inventore, asperiores autem, quidem vel tempore fugit ut, aliquam nihil consectetur voluptatibus laborum. Totam, maxime! Praesentium ex rerum corporis aut velit maxime. Inventore aspernatur sequi nam dicta reprehenderit perferendis molestiae consectetur omnis iure sapiente, sed quidem minima distinctio expedita, excepturi enim nihil minus exercitationem ipsum aliquam ab dolor fugiat itaque! Temporibus sint velit, soluta nisi hic est numquam? Odio similique est voluptatibus iste, ad provident molestias officiis in error suscipit atque eaque possimus aut exercitationem enim dignissimos consequuntur voluptas modi tenetur architecto delectus fugit porro nulla quo. Veritatis doloribus impedit facilis possimus laboriosam soluta, accusantium fugit, libero sequi quis iste? Inventore rerum cupiditate porro, facilis voluptate consectetur asperiores vel. Quos pariatur quo autem, eaque tenetur alias quasi eum excepturi tempora iure sapiente! Rerum, libero? Voluptate dignissimos cupiditate debitis amet similique expedita? Reprehenderit voluptas explicabo at voluptatem pariatur deleniti sunt sint aliquid dolorum cum soluta quibusdam doloremque, iure sapiente quas maxime vero veniam, placeat, dignissimos possimus quos iste. Ad tenetur placeat rem sequi quasi dolorem voluptatum, repellendus neque nihil, eligendi perspiciatis laboriosam! Optio magni dignissimos voluptate qui. Voluptatum beatae facere nesciunt omnis libero odio tenetur ducimus enim maiores. Maxime ipsa at voluptas odio est necessitatibus doloribus nemo, laboriosam ut labore dolorem cum libero reprehenderit quos dolore quasi accusamus assumenda voluptatum a corrupti ad minus. Expedita rem, dolorum repellendus commodi recusandae quidem beatae voluptatum cumque sit. Rerum omnis voluptatum blanditiis culpa dolor laborum nemo labore excepturi provident eos? Ratione quasi quae laudantium facere, unde nihil eius dicta porro aperiam ab nam reiciendis temporibus praesentium aut culpa, dignissimos vel molestias maxime consequuntur, harum dolor. Veritatis iusto itaque neque nobis enim reprehenderit saepe deserunt illum? Quas distinctio delectus saepe consequatur doloremque expedita reiciendis provident dicta quisquam. Corrupti ratione dolor autem.</p>
    {{this.profile}}
    <h3></h3>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { userService } from '@/services'
import Navbar from '@/components/Navbar'
import ProfileHeader from '@/components/Profile.header'
import PostTemplate from '@/components/post'
import JQuery from 'jquery'
let $ = JQuery

export default {
  name: 'ProfileComponent',
  components: { 'nav-bar': Navbar, 'profile-header': ProfileHeader, 'post-template': PostTemplate},
  data () {
    return {
      profile: null,
      coverImgProps: {
        src: 'https://picsum.photos/300/150/?image=41',
        class: ''
      },
      profileImgProps: {
        src: 'https://picsum.photos/300/150/?image=41',
        width: 300,
        height: 300,
        class: ''
      }
    }
  },
  computed: {
    ...mapState({
      account: state => state.account
    }),
    is_editable () {
      return this.$route.params.id === this.account.user.personalProfile._id
    }
  },
  methods: {
    header_params () {
      var headinfos = {
        name: this.profile.name,
        familyName: this.profile.familyName,
        username: this.profile.username,
        followers_count: this.profile.followers.length,
        posts_count: this.profile.posts.length
      }
      return {
        profileImgProps: this.profileImgProps,
        coverImg: 'https://picsum.photos/300/150/?image=41',
        headinfos: headinfos
      }
    }
  },
  beforeCreate: function () {},
  mounted: function () {
    var current_user = JSON.parse(localStorage.getItem('user'))
    var current_profile = current_user.personalProfile

    if (this.$route.params.id === current_profile._id) {
      this.profile = {
        ...current_profile,
        familyName: current_user.familyName,
        name: current_user.name
      }
      this.$emit('pass_params', this.header_params())
    } else {
      userService.getProfile(this.$route.params.id).then(
        profile => {
          this.profile = profile
          this.$emit('pass_params', this.header_params())
        },
        error => alert(error)
      )
    }
  }
}
</script>

<style>
#sections .nav-tabs .nav-link{
  background-color: transparent;
  color:#495057;
  border:none !important;
  border-top-left-radius: .25rem;
  border-top-right-radius: .25rem;
  border-bottom: 1px solid rgba(77, 76, 76,0.5) !important;

}
#sections .nav-tabs .nav-link:active{
  border:none !important;
}
#sections .nav-tabs .nav-link.active{
  background-color: transparent;
  border:none;
  border-bottom: 2px solid #007bff !important;
  color:#007bff;

}
</style>



