<template>
<div >
  <b-card v-for="i in [1,2,3,4,5,6,7]" class="shadow-lg my-5">
    <b-card-header class="bg-white border-0">
      <div class="d-flex justify-content-between align-items-between">
        <div class="d-flex justify-content-between align-items-center">
          <div class="mr-2">
            <img class="rounded-circle" width="45" src="https://picsum.photos/50/50" alt />
          </div>
          <div class="ml-1">
            <div class="h5 m-0">Miracles Lee Cross</div>
            <div class="text-muted d-flex justify-content-start post-time" >10 minutes ago</div>
          </div>
        </div>
        <div>
          
          <b-dropdown  no-caret size="lg" variant="white" right >
          <!-- Using 'button-content' slot -->
          <template v-slot:button-content>
            <b-icon variant="secondary" icon="three-dots"></b-icon>
          </template>

          <b-dropdown-item to="#">
            <b-row>
              <b-col md="3">
                <b-icon icon="gear-fill"></b-icon>
              </b-col>
              <b-col>Settings</b-col>
            </b-row>
          </b-dropdown-item>
          <b-dropdown-item to="#">
            <b-row>
              <b-col md="3">
                <b-icon icon="power"></b-icon>
              </b-col>
              <b-col >Sign Out</b-col>
            </b-row>
          </b-dropdown-item>
        </b-dropdown>
        </div>
      </div>
    </b-card-header>
    <b-card-body>
      
      <p class="card-text">
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Quo recusandae nulla rem eos ipsa praesentium esse magnam nemo dolor
        sequi fuga quia quaerat cum, obcaecati hic, molestias minima iste voluptates.
      </p>
    </b-card-body>
    <b-card-footer>
      <a  class="card-link">
        <button class="icobutton icobutton--heart"><span class="fa fa-heart"></span></button>
      </a>
      <a  class="card-link">
        <i class="fa fa-comment"></i> Comment
      </a>
      <a  class="card-link">
        <i class="fa fa-share"></i> Share
      </a>
    </b-card-footer>
  </b-card>
</div>
</template>

<script>
import {init} from '@/assets/js/custom-animations.js'
window.onload=function(){
  // init()
}
export default {
  name: "PostTemplate",
  template: "<post-template/>",
  data() {
    return {
      params: {}
    };
  },
  mounted:function(){
    init()
  }
};
</script>

<style scoped>
.pseudo-title{
    margin-left: 0;
}
/* Icons button */
.icobutton {
	position: relative;
	color: #c0c1c3;
	border: 0;
	background: none;
	overflow: visible;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}

.icobutton .fa {
	display: block;
	padding: 0 0.1em;
}



/* fix for mo.js */
.icobutton svg {
	left: 0;
}

.icobutton:hover,
.icobutton:focus {
	outline: none;
}


</style>


