
/* eslint-disable */
<template>
  <div>
    <div style="background-color: #999999;">
      <div class="limiter">
        <div class="container-login100">
          <div class="login100-more" style="background-image: url('images/bg-01.jpg');"></div>
          <div class="wrap-login100 p-l-50 p-r-50 p-t-72 p-b-50">
            <signup-component v-if="signup_visible" :new_user="new_user" @signupInfosChanged="changeSignupInfos"/>
            <login-component v-else :user="user" @logInfosChanged="changeloginInfos"/>

            <div class="container-login100-form-btn">
              <div class="wrap-login100-form-btn">
                <div class="login100-form-bgbtn"></div>

                <button
                  class="login100-form-btn"
                  @click.prevent="submit()"
                >{{signup_visible? 'Sign Up':'Sign in'}}</button>
              </div>

              <a
                href="#"
                @click.prevent="switch_section();refresh()"
                class="dis-block txt3 hov1 p-r-30 p-t-10 p-b-10 p-l-30"
              >
                {{signup_visible? 'Sign in':'Sign Up'}}
                <i class="fa fa-long-arrow-right m-l-5"></i>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoginComponent from './Login'
import SignUpComponent from './SignUp'
import JQuery from 'jquery'
import { mapState, mapActions } from 'vuex'
import { initialize } from '../assets/js/main.js'
let $ = JQuery

export default {
  name: 'AuthentificationComponent',
  components: {
    'login-component': LoginComponent,
    'signup-component': SignUpComponent
  },
  props: ['show_signup'],
  data () {
    return {
      signup_visible: true,
      user: { email: '', password: '' },
      new_user: { first_name: '',last_name: '',birthdate:'',gender:'m', username: '', email: '', password: '' }
    }
  },
  computed: {
    ...mapState('account', ['status'])
  },
  created: function () {
    this.$set(this, 'signup_visible', this.show_signup)
  },
  mounted: function () {
    document.addEventListener('change', function () {
      // initialize(this)
    })
  },
  methods: {
    ...mapActions('account', ['register', 'login', 'logout']),
    changeloginInfos (val) {
      this.user = val
    },
    changeSignupInfos (val) {
      this.new_user = val
    },
    switch_section () {
      this.signup_visible = !this.signup_visible
    },
    refresh () {
      let app = this
      document
        .querySelectorAll('.validate-form .input100')
        .forEach(function (el) {
          console.log('here')
          app.hideValidate(el)
        })
      // initialize(this)
    },
    validate (input) {
      if (
        $(input).attr('type') === 'email' ||
        $(input).attr('name') === 'email'
      ) {
        if (
          $(input)
            .val()
            .trim()
            .match(
              /^([a-zA-Z0-9_\-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{1,5}|[0-9]{1,3})(\]?)$/
            ) === null
        ) {
          return false
        }
      } else {
        if (
          $(input)
            .val()
            .trim() === ''
        ) {
          return false
        }
      }
    },

    showValidate (input) {
      var thisAlert = $(input).parent()

      $(thisAlert).addClass('alert-validate')
    },

    hideValidate (input) {
      var thisAlert = $(input).parent()

      $(thisAlert).removeClass('alert-validate')
    },
    submit () {
      var input = document.querySelectorAll('.validate-input .input100')
      console.log(input.length)
      var check = true
      for (var i = 0; i < input.length; i++) {
        if (this.validate(input[i]) === false) {
          this.showValidate(input[i])
          check = false
        }
      }

      if (check) {
        if (!this.signup_visible) this.login(this.user)
        else this.register(this.new_user)
      }
    }
  }
}
</script>

<style>
@import "../assets/vendor/bootstrap/css/bootstrap.min.css";
@import "../assets/fonts/font-awesome-4.7.0/css/font-awesome.min.css";
@import "../assets/fonts/Linearicons-Free-v1.0.0/icon-font.min.css";
@import "../assets/fonts/iconic/css/material-design-iconic-font.min.css";
@import "../assets/vendor/animate/animate.css";
@import "../assets/vendor/css-hamburgers/hamburgers.min.css";
@import "../assets/vendor/animsition/css/animsition.min.css";
@import "../assets/vendor/select2/select2.min.css";
@import "../assets/vendor/daterangepicker/daterangepicker.css";
@import "../assets/css/util.css";
@import "../assets/css/main.css";
</style>



