<template>
  <div v-if="Object.entries(params).length > 0">
    <div id="cover-pic-container" class="w-100">
      <b-img :src="params.coverImg" fluid-grow alt="Fluid-grow image"></b-img>
      <div id="profile-pic-container">
        <b-row class="d-flex justify-content-center">
          <b-col class="d-flex justify-content-center">
            <b-row class="d-flex justify-content-center">
              <b-col></b-col>
              <b-col class="d-flex justify-content-center">
                <div class="mt-auto mb-1 align-self-center">
                  <div class="align-self-end">
                    <h2>{{params.headinfos.name }}</h2>
                  </div>
                </div>
                <div>
                  <b-img
                    id="params.headinfos-pic"
                    v-bind="params.profileImgProps "
                    rounded="circle"
                    alt="Circle image"
                  ></b-img>
                </div>
                <div class="mt-auto mb-1 align-self-center">
                  <div class="align-self-end">
                    <h2>{{params.headinfos.familyName }}</h2>
                  </div>
                </div>
              </b-col>
              <b-col></b-col>
            </b-row>
          </b-col>
        </b-row>
        <b-row align-content="center">
          <b-col>
            <b-row class="d-flex justify-content-center">
              <b-col class="text-secondary font-weight-bold">{{params.headinfos.posts_count }}</b-col>
            </b-row>
            <b-row class="d-flex justify-content-center">
              <b-col class="text-dark">Posts</b-col>
            </b-row>
          </b-col>
          <b-col>
            <b-row align-content="center">
              <b-col class="text-secondary font-weight-bold">{{params.headinfos.followers_count }}</b-col>
            </b-row>
            <b-row align-content="center">
              <b-col class="text-dark">Followers</b-col>
            </b-row>
          </b-col>
          <b-col>
            <b-row align-content="center">
              <b-col class="text-secondary font-weight-bold">{{params.headinfos.followers_count }}</b-col>
            </b-row>
            <b-row align-content="center">
              <b-col class="text-dark">Follows</b-col>
            </b-row>
          </b-col>
        </b-row>

        
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import JQuery from "jquery";
let $ = JQuery;

export default {
  name: "ProfileHeader",
  template: "<profile-header/>",
  data() {
    return {
      params: {}
    };
  },
  computed: {
    ...mapState("account", ["status"]),
    container_width() {
      return document.getElementById("profile-pic-container").style.width;
    }
  },
  methods: {
    ...mapActions("account", ["logout"]),
    update_infos(infos) {
      this.params = infos;
      console.log("Received params :", this.params);
    }
  },
  created: function() {
    this.$parent.$on("pass_params", this.update_infos);
  },
  mounted: function() {
    let app = this;
    function init() {
      app.params.profileImgProps.width =
        Number($("#cover-pic-container").width()) / 4;
      app.params.profileImgProps.height = app.params.profileImgProps.width;
      $("#profile-pic-container").css(
        "margin-top",
        -0.5 * Number(app.params.profileImgProps.height) + "px"
      );
    }
    $(window).on("resize", function() {
      if (app.params.profileImgProps) init();
    });
    $(window).on("load", function() {
      if (app.params.profileImgProps) init();
    });
  }
};
</script>

<style>
#cover-pic-container {
  width: 100vw;
  padding: 0;
  /* position: relative; */
}
#profile-pic-container {
  /* position: absolute; */
  width: 100vw;

  /* background-color: red; */
}
#profile-pic {
}
</style>



