<template>
  <div class="container-fluid">
    <div class="row">
      <div class="md-6 offset-3">
        <h3 variant="secondary">You are not logged in. You will be redirected to login page...</h3>
      </div>
    </div>
    <div class="row">
      <div class="w-100 text-center">
        <b-spinner  style="width: 3rem; height: 3rem;" class="m-5" variant="primary" label="Please wait..."></b-spinner>
        <!-- <h3>fssldkjglkdjg</h3> -->
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'RedirectComponent',
  data () {
    return {}
  },
  created: function () {
    setTimeout(() => this.$router.push({path: '/auth',props:{show_signup:false}}), 10000)
  }
}
</script>

<style>
</style>



