<template>
  <b-navbar sticky class="w-100" toggleable="lg" type="primary" variant="light">
    <b-navbar-brand to="#" v-b-tooltip.hover title="Brand">Brand</b-navbar-brand>

    <b-navbar-toggle target="nav-collapse">
      
        <b-icon variant="primary" icon="list"></b-icon>
      
    </b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item to="#" v-b-tooltip.hover title="Profile">
          <b-icon icon="person-fill"></b-icon>
        </b-nav-item>
        <b-nav-item to="#" v-b-tooltip.hover title="People's stories">
          <b-icon icon="collection-fill"></b-icon>
        </b-nav-item>
      </b-navbar-nav>

      <b-navbar-nav class="ml-auto">
        <b-nav-form>
          <b-form-input size="sm" class="mr-sm-2" placeholder="Search"></b-form-input>
          <b-button size="sm" class="my-2 my-sm-0" type="submit">
            <b-icon icon="search"></b-icon>
          </b-button>
        </b-nav-form>

        <b-nav-item to="#" v-b-tooltip.hover title="Messages">
          <b-icon icon="envelope-fill"></b-icon>
        </b-nav-item>
        <b-nav-item to="#" v-b-tooltip.hover title="notifications">
          <b-icon icon="bell-fill"></b-icon>
        </b-nav-item>

        <b-nav-item-dropdown size="lg" right>
          <!-- Using 'button-content' slot -->
          <template v-slot:button-content>
            <!-- <em>User</em> -->
          </template>

          <b-dropdown-item to="#">
            <b-row>
              <b-col md="3">
                <b-icon icon="gear-fill"></b-icon>
              </b-col>
              <b-col>Settings</b-col>
            </b-row>
          </b-dropdown-item>
          <b-dropdown-item to="#">
            <b-row>
              <b-col md="3">
                <b-icon icon="power"></b-icon>
              </b-col>
              <b-col @click="logout()">Sign Out</b-col>
            </b-row>
          </b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
import JQuery from "jquery";
import { mapState, mapActions } from "vuex";
let $ = JQuery;

export default {
  name: "Navbar",
  template: "<nav-bar/>",
  data() {
    return {
      transparent: true
    };
  },
  computed: {
    ...mapState("account", ["status"])
  },
  methods: {
    ...mapActions("account", ["logout"])
  },
  mounted: function() {
    $(window).scroll(function() {
      if ($(document).scrollTop() < 50) {
        $(".navbar").addClass("transparent");
      } else {
        $(".navbar").removeClass("transparent");
      }
    })
  }
};
</script>

<style scoped>
.navbar {
  position: fixed;
}
.navbar-brand {
  color: #e8519e;
  font-size: 150%;
  font-weight: bold;
}
.nav-item,
.nav-item-dropdown {
  font-weight: bold;
}
.nav-item:hover,
.nav-item-dropdown:hover {
  color: #e8519e;
}
.transparent {
  background-color: transparent !important;
}
</style>



