/* eslint-disable */
<template>
  <form class="login100-form validate-form">
    <span class="login100-form-title p-b-59">Sign Up</span>

    <div class="wrap-input100 validate-input" data-validate="Name is required">
      <span class="label-input100">First Name</span>
      <input
        class="input100"
        type="text"
        name="first_name"
        placeholder="Name..."
        @change="set_infos()"
        v-model="$props.new_user.first_name"
      />
      <span class="focus-input100"></span>
    </div>
    <div class="wrap-input100 validate-input" data-validate="Name is required">
      <span class="label-input100">Last Name</span>
      <input
        class="input100"
        type="text"
        name="last_name"
        placeholder="Name..."
        @change="set_infos()"
        v-model="$props.new_user.last_name"
      />
      <span class="focus-input100"></span>
    </div>

    <div class="w-100 gender-container" data-validate="You must specify your gender">
      <span class="label-input100 label-gender">Gender</span>
      <div class="d-flex justify-content-center">
        <div class="custom-control custom-radio custom-control-inline">
          <input
            class="custom-control-input"
            type="radio"
            name="gender"
            value="m"
            id="male"
            @change="set_infos()"
            v-model="$props.new_user.gender"
          />
          <label class="custom-control-label" for="male">Male</label>
        </div>
        <div class="custom-control custom-radio custom-control-inline">
          <input
            class="custom-control-input"
            type="radio"
            name="gender"
            value="f"
            id="female"
            @change="set_infos()"
            v-model="$props.new_user.gender"
          />
          <label class="custom-control-label" for="female">Female</label>
        </div>
      </div>
    </div>

    <div class="wrap-input100 validate-input" data-validate="Name is required">
      <span class="label-input100">Date of birth</span>
      <input
        class="input100"
        type="date"
        name="birthdate"
        @change="set_infos()"
        v-model="$props.new_user.birthdate"
      />
      <span class="focus-input100"></span>
    </div>

    <div
      class="wrap-input100 validate-input"
      data-validate="Valid email is required: exampl@abc.xyz"
    >
      <span class="label-input100">Email</span>
      <input
        class="input100"
        type="email"
        name="email"
        placeholder="Email addess..."
        @change="set_infos()"
        v-model="$props.new_user.email"
      />
      <span class="focus-input100"></span>
    </div>

    <div class="wrap-input100 validate-input" data-validate="Username is required">
      <span class="label-input100">Username</span>
      <input
        class="input100"
        type="text"
        name="username"
        placeholder="Username..."
        @change="set_infos()"
        v-model="$props.new_user.username"
      />
      <span class="focus-input100"></span>
    </div>

    <div class="wrap-input100 validate-input" data-validate="Password is required">
      <span class="label-input100">Password</span>
      <input
        class="input100"
        type="password"
        name="pass"
        placeholder="*************"
        @change="set_infos()"
        v-model="$props.new_user.password"
      />
      <span class="focus-input100"></span>
    </div>

    <div class="wrap-input100 validate-input" data-validate="Repeat Password is required">
      <span class="label-input100">Repeat Password</span>
      <input class="input100" type="password" name="repeat-pass" placeholder="*************" />
      <span class="focus-input100"></span>
    </div>

    <div class="flex-m w-full p-b-33">
      <div class="contact100-form-checkbox">
        <input class="input-checkbox100" id="ckb1" type="checkbox" name="remember-me" />
        <label class="label-checkbox100" for="ckb1">
          <span class="txt1">
            I agree to the
            <a href="#" class="txt2 hov1">Terms of User</a>
          </span>
        </label>
      </div>
    </div>
  </form>
</template>
<script>
import { initialize } from "../assets/js/main.js";
export default {
  name: "SignUpComponent",
  template: "<signup-component/>",
  props: ["new_user"],
  data() {
    return {
      // $props.new_user: { first_name: '',last_name: '', username: '', email: '', password: '' }
    };
  },
  mounted: function() {
    initialize();
  },
  methods: {
    set_infos() {
      console.log(this.$props.new_user);
      this.$emit("signupInfosChanged", this.$props.new_user);
    }
  },
  errorCaptured(err, vm, info) {
    console.log(`ErrorCaptured: ${err.toString()}\ninfo: ${info}`);
    return false;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input[name="birthdate"] {
  background: #fff url(../assets/img/calender.png) 97% 50% no-repeat;
}
[type="date"]::-webkit-inner-spin-button {
  display: none;
}
[type="date"]::-webkit-calendar-picker-indicator {
  opacity: 0;
}

.label-gender {
  display: block;
  text-align: center;
  width: 100%;
  margin-bottom: 20px;
}
.gender-container {
  margin-bottom: 45px;
}
</style>



